<style scoped>
img {width: 2.5rem;height: 2.5rem;border-radius: 50%;margin-right: 0.5rem;}
</style>>
<template>
<div class="bg">
  <div class="content" v-if="!editShow">
      
      <el-button type="primary" size="mini" @click="hEdit(0)">+添加优惠券</el-button>
    <el-table
      :data="tableData"
      style="width: 100%;margin-top:0.5rem" :cell-style="{fontSize:'0.65rem'}" :header-cell-style="{background:'#eef1f6',color:'#606266',fontSize:'0.6rem'}">
      <el-table-column  align="center" width="30"
        label=""
      >
        <template slot-scope="scope">
          <el-checkbox></el-checkbox>
        </template>
      </el-table-column>
      <el-table-column  align="center" 
        label="优惠券名称"
      >
        <template slot-scope="scope">
          <span >满减</span>
        </template>
      </el-table-column>
      <el-table-column  align="center" 
        label="使用条件"
      >
        <template slot-scope="scope">
          <span >满100使用</span>
        </template>
      </el-table-column>
      <el-table-column  align="center" 
        label="领取中心"
      >
        <template slot-scope="scope">
          <span >不显示</span>
        </template>
      </el-table-column>
     
        <el-table-column  align="center" label="创建时间">
            <template slot-scope="scope">
            <span >2020.02.03 22:43:53</span>
            </template>
        </el-table-column>
        
      <el-table-column  align="center"  label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="hEdit(1)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="p20 textRight">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[100, 200, 300, 400]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="400">
      </el-pagination>
    </div>
  </div>
  <!-- 编辑用户信息 -->
  <div class="content p20" v-if="editShow">
    <div class="blue mb20  back" @click="back">
      <i class="iconfont icon-cs-fh-1 "></i><a class="font18 pl20">返回</a>
    </div>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm p20">
     
      <el-form-item label="优惠券名称" >
      <el-input style="width:30%"></el-input>
      </el-form-item>
      <el-form-item label="使用条件" >
        <div class="row"><span class="pl10 pr10">满</span><el-input style="width:10%"></el-input><span class="pr10 pl10">使用</span></div>
      </el-form-item>      
      <el-form-item label="领取中心" >
         <el-radio v-model="radio" label="1">显示</el-radio>
         <el-radio v-model="radio" label="2">不显示</el-radio>
      </el-form-item>      
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="back">返回列表</el-button>
      </el-form-item>
    </el-form>
  </div>
</div>
</template>

<script>

  export default {
    data() {
      return {
          isClear: false,
        detail:"",
        radio:0,
        tableData: [{
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          num1:1,
          num2:1
          
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄',
           num1:1,
          num2:1
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄',
           num1:1,
          num2:1
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
           num1:1,
          num2:1
        }],
          currentPage1: 5,
        currentPage2: 5,
        currentPage3: 5,
        currentPage4: 4,
        editShow:false,
        ruleForm: {
          name: '',
          nickname: '',
          tel: '',
          resource: '',
         
        },
        rules: {
          name: [
            { required: true, message: '请输入真实姓名', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          tel: [
            { required: true, message: '请输入电话号码', trigger: 'change' }
          ],
          nickname: [
            { required: true, message: '请输入微信号码', trigger: 'change' }
          ],
          
        },
         options: [{
          value: '选项1',
          label: '分类1'
          }, {
          value: '选项2',
          label: '分类1'
          }, {
          value: '选项3',
          label: '分类1'
          }, {
          value: '选项4',
          label: '分类1'
        }],
        value: ''
      }
    },
    methods: {
       change(val) {
        console.log(val)
        
      },
      hEdit(index) {
        console.log(index);
        var that=this;
        that.editShow=true;
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      back() {
       var that=this;
        that.editShow=!that.editShow;
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
      
    },
      //注册组件
    components: {
    
     
    },
  }
</script>


